// 
import {init} from 'snabbdom'
import {h} from "h";

// 
import {styleModule} from 'style'
import {eventListenersModule} from 'eventListener'

// 
let patch = init([
  eventListenersModule,
  styleModule
])

// h 
let vnode = h('div', {
  style: {
    backgroundColor: 'red'
  },
  on: {
    click: eventHandler
  }
}, [
  h('h1', 'hello sam'),
  h('p', 'p')
])

function eventHandler () {
  console.log('')
}

let app = document.querySelector('#app')

patch(app, vnode)